.header.singleplayer {
    font-size: 2vw;
}
.header.splitscreen {
    font-size: 1.5vw;
    padding-top:0.25vw;
}

.header-clock,
.header-title {
    margin-top: 0.25vw;
    float: left;
}

.header-clock {
    margin-left: 1vw;
}

.header-buttons {
    float: right;
}

.header-buttons > div {
    float: left;
}

.icon-container {
    margin-left: 1vw;
    float: left;
    text-align: center;
}

.heart-container {
    margin-left: 1vw;
    float: left;
    text-align: left;
}

.header-buttons > div > button {
    padding-top: 0vw;
    border-radius: 0.5vw;
    border-color: gray;
    border-style: none;

    margin-top: 0.25vw;
    background-color: transparent;
}
.singleplayer  .header-buttons > div > button {
    width: 5vw;
}

.splitscreen .header-buttons > div > button {
    width: 3.5vw;
}

.menu {
    margin-top: 2vw;
    margin: 1vw;
    padding: 0.5vw;
    width: 20vw;
    float: left;

    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    color: white;
    text-align: left;
    background-color: rgba(0, 0, 0, 0.95);
    border-radius: 0.5vw;
}


.menu > button.indented {
    padding-left: 3vw;
}

.menu > button.active {
    color: white;
}

.menu > button {
    padding: 0.2vw;
    padding-left: 1vw;
    margin-top: 0.1vw;
    margin-bottom: 0.2vw;

    font-size: 2vw;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    text-align: left;
    color: gray;
    
    border-color: light-gray;
    background-color: rgba(0, 0, 0, 0.95);
    border-radius: 0.25vw;
}

.menu > button {
    width: 100%;
}

.menu > button.highlight {
    border-color: blue;
}

.menu > button > .icon {
    float: left;
    padding-right: 1vw;
}

.startscreen-layout {
    padding: 0.5vw;
    margin-top: 0vw;
    margin: 1vw;
    width: 50vw;
    float: left;
    height: 46vw;
    background-color: red;
    background-color: rgba(0, 0, 0, 0.95);
    border-radius: 0.25vw;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    color: white;
    position: relative;
}

.startscreen-jail {
    position: absolute;
    left: 0;
    top:0;
    right:0;
    bottom:0; 
}

.menu > .title,
.startdetails > .title {
    padding-top: 0.5vw;
    padding-left: 1vw;
    width: 100%;
    font-size: 3vw;
}


.controls .left {
    float: left;
    width: 50%;
}

.controls .right {
    float: left;
    width: 50%;
}
.rating .content .section {
    padding:0vw;
    padding-right:2vw;
    padding-left:2vw;
}

